<template>
<div>
    <!-- HOVEDKATEGORI - KONTOR- OG ADMINISTRASJONSFAGET -->
    <p class="kategori-header" @click="kontorAdmKategori = !kontorAdmKategori">Kontor- og administrasjonsfaget &#8628;</p>
    <div v-show="kontorAdmKategori">

      <!-- UNDERKATEGORI - KONTORSERVICE -->
      <p class="underkategori-header" @click="kontorservice_underkat = !kontorservice_underkat">Kontorservice &#8628;</p>
      <div v-show="kontorservice_underkat">
          <label
            v-for="lpm in kontorservice" 
            :key="lpm.id"
            class="lpm-list-item"
            :class="{chosen : lpm.checked}"
          >
          <input
            class="checkmark"
            type="checkbox"
            v-model="lpm.checked"
            @change="addLpm(lpm)"
          >
          <span class="lpm-text">{{ lpm.maal }}</span>
        </label>
      </div>

      <!-- UNDERKATEGORI - IKT-TJENESTER -->
      <p class="underkategori-header" @click="ikt_underkat = !ikt_underkat">IKT-tjenester &#8628;</p>
      <div v-show="ikt_underkat">
          <label
            v-for="lpm in ikt" 
            :key="lpm.id"
            class="lpm-list-item"
            :class="{chosen : lpm.checked}"
          >
          <input
            class="checkmark"
            type="checkbox"
            v-model="lpm.checked"
            @change="addLpm(lpm)"
          >
          <span class="lpm-text">{{ lpm.maal }}</span>
        </label>
      </div>

      <!-- UNDERKATEGORI - ØKONOMI -->
      <p class="underkategori-header" @click="okonomi_underkat = !okonomi_underkat">Økonomi &#8628;</p>
      <div v-show="okonomi_underkat">
          <label
            v-for="lpm in okonomi_lpms" 
            :key="lpm.id"
            class="lpm-list-item"
            :class="{chosen : lpm.checked}"
          >
          <input
            class="checkmark"
            type="checkbox"
            v-model="lpm.checked"
            @change="addLpm(lpm)"
          >
          <span class="lpm-text">{{ lpm.maal }}</span>
        </label>
      </div>
    </div>

    <!-- HOVEDKATEGORI - SALGSFAGET -->
    <p class="kategori-header" @click="salgsfagetKategori = !salgsfagetKategori">Salgsfaget &#8628;</p>
    <div v-show="salgsfagetKategori">

    <!-- UNDERKATEGORI - SALGSFORBEREDELSE -->
    <p class="underkategori-header" @click="salgsforberedelse_underkat = !salgsforberedelse_underkat">Salgsforberedelse &#8628;</p>
    <div v-show="salgsforberedelse_underkat">
      <label
        v-for="lpm in salgsforberedelse_lpms" 
        :key="lpm.id"
        class="lpm-list-item"
        :class="{chosen : lpm.checked}"
      >
        <input
          class="checkmark"
          type="checkbox"
          v-model="lpm.checked"
          @change="addLpm(lpm)"
        >
        <span class="lpm-text">{{ lpm.maal }}</span>
      </label>
    </div>

    <!-- UNDERKATEGORI - SALG OG OPPFØLGING -->
    <p class="underkategori-header" @click="salgOgOppfolgning_underkat = !salgOgOppfolgning_underkat">Salg og oppfølging &#8628;</p>
    <div v-show="salgOgOppfolgning_underkat">
      <label
        v-for="lpm in salg_og_oppf_lpms" 
        :key="lpm.id"
        class="lpm-list-item"
        :class="{chosen : lpm.checked}"
      >
        <input
          class="checkmark"
          type="checkbox"
          v-model="lpm.checked"
          @change="addLpm(lpm)"
        >
        <span class="lpm-text">{{ lpm.maal }}</span>
      </label>
    </div>
    </div>

    <!-- HOVEDKATEGORI - SIKKERHETSFAGET -->
    <p class="kategori-header" @click="sikkerhetsfagetKategori = !sikkerhetsfagetKategori">Sikkerhetsfaget &#8628;</p>
    <div v-show="sikkerhetsfagetKategori">

    <!-- UNDERKATEGORI - SIKKERHETSPLANLEGGING -->
    <p class="underkategori-header" @click="sikkerhetsplanlegging_underkat = !sikkerhetsplanlegging_underkat">Sikkerhetsplanlegging &#8628;</p>
    <div v-show="sikkerhetsplanlegging_underkat">
      <label
        v-for="lpm in sikkerhetsplanlegging_lpms" 
        :key="lpm.id"
        class="lpm-list-item"
        :class="{chosen : lpm.checked}"
      >
        <input
          class="checkmark"
          type="checkbox"
          v-model="lpm.checked"
          @change="addLpm(lpm)"
        >
        <span class="lpm-text">{{ lpm.maal }}</span>
      </label>
    </div>

    <!-- UNDERKATEGORI - SIKKERHETSARBEID -->
    <p class="underkategori-header" @click="sikkerhetsarbeid_underkat = !sikkerhetsarbeid_underkat">Sikkerhetsarbeid &#8628;</p>
    <div v-show="sikkerhetsarbeid_underkat">
      <label
        v-for="lpm in sikkerhetsarbeid_lpms"
        :key="lpm.id"
        class="lpm-list-item"
        :class="{chosen : lpm.checked}"
      >
        <input
          class="checkmark"
          type="checkbox"
          v-model="lpm.checked"
          @change="addLpm(lpm)"
        >
        <span class="lpm-text">{{ lpm.maal }}</span>
      </label>
    </div>

    <!-- UNDERKATEGORI - RAPPORTERING -->
    <p class="underkategori-header" @click="rapportering_underkat = !rapportering_underkat">Rapportering &#8628;</p>
    <div v-show="rapportering_underkat">
      <label
        v-for="lpm in rapportering_lpms" 
        :key="lpm.id"
        class="lpm-list-item"
        :class="{chosen : lpm.checked}"
      >
        <input
          class="checkmark"
          type="checkbox"
          v-model="lpm.checked"
          @change="addLpm(lpm)"
        >
        <span class="lpm-text">{{ lpm.maal }}</span>
      </label>
    </div>
  </div>
</div>
</template>

<script>
import { mapGetters } from 'vuex'
import _ from 'lodash'

export default {
    data () {
        return {
            //BOOLSKE VARIABLER FOR V-SHOW (KATEGORIER)
            kontorAdmKategori: true,
            kontorservice_underkat: false,
            ikt_underkat: false,
            okonomi_underkat: false,

            salgsfagetKategori: true,
            salgsforberedelse_underkat: false,
            salgOgOppfolgning_underkat: false,

            sikkerhetsfagetKategori: true,
            sikkerhetsplanlegging_underkat: false,
            sikkerhetsarbeid_underkat: false,
            rapportering_underkat: false
        }
    },
    methods: {
        // CHECKER / UNCHECKER LPM OG COMMITER METODE UT FRA lpm.checked
        // SE I STORE HVA SOM COMMITES
        addLpm (lpm) {
            if (lpm.checked) {
                this.$store.commit('addLpm', lpm)
            } else {
            this.$store.commit('removeLpm', lpm)
            }
        }   
    },
    computed: {
      ...mapGetters([
        'get_vg3_kontor_adm_lpms',
        'get_vg3_salgsfaget_lpms',
        'get_vg3_sikkerhetsfaget_lpms'
      ]),
      kontorservice () {
        return _.pickBy(this.$store.getters.get_vg3_kontor_adm_lpms, function (lpm) {
          return lpm.emne === 'Kontorservice'
        })
      },
      ikt () {
        return _.pickBy(this.$store.getters.get_vg3_kontor_adm_lpms, function (lpm) {
          return lpm.emne === 'IKT-tjenester'
        })
      },
      okonomi_lpms () {
        return _.pickBy(this.$store.getters.get_vg3_kontor_adm_lpms, function (lpm) {
          return lpm.emne === 'Økonomi'
        })
      },
      salgsforberedelse_lpms () {
        return _.pickBy(this.$store.getters.get_vg3_salgsfaget_lpms, function (lpm) {
          return lpm.emne === 'Salgsforberedelse'
        })
      },
      salg_og_oppf_lpms () {
        return _.pickBy(this.$store.getters.get_vg3_salgsfaget_lpms, function (lpm) {
          return lpm.emne === 'Salg og oppfølging'
        })
      },
      sikkerhetsplanlegging_lpms () {
        return _.pickBy(this.$store.getters.get_vg3_sikkerhetsfaget_lpms, function (lpm) {
          return lpm.emne === 'Sikkerhetsplanlegging'
        })
      },
      sikkerhetsarbeid_lpms () {
        return _.pickBy(this.$store.getters.get_vg3_sikkerhetsfaget_lpms, function (lpm) {
          return lpm.emne === 'Sikkerhetsarbeid'
        })
      },
      rapportering_lpms () {
        return _.pickBy(this.$store.getters.get_vg3_sikkerhetsfaget_lpms, function (lpm) {
          return lpm.emne === 'Rapportering'
        })
      }
    }
} 
</script>

<style scoped>
 .lpm-list-item{
    display:inline-block;
    border:1px solid white;
    border-radius:5px;
    width:85%;
    padding:7px;
    padding-left:50px;
    margin-left:auto;
    margin-right:auto;
    margin-top:10px;
    cursor:pointer;
    text-align: start;
  }

  .checkmark{
    height:15px;
    width:15px;
  }

  .chosen{
    background-color: rgba(0,128,0 ,.7);
  }

  .chosen:hover{
    background-color: rgba(236, 15, 15, 0.7);
  }

  .lpm-list-item input {
    vertical-align: middle;
  }

  .lpm-text{
    vertical-align: middle;
  }

  .lpm-list-item-selected{
    background-color: rgba(0,128,0 ,.7);
  }

  .kategori-header{
    cursor:pointer;
    margin-top:50px;
    font-size: 1.15rem;
  }

  .underkategori-header{
    color:#131414;
    cursor:pointer;
  }
</style>
